import React, { useRef, Suspense } from "react";
import { Canvas, useFrame } from "@react-three/fiber";
import { Html, OrbitControls } from "@react-three/drei";
import { Ship } from "./Ship";
import "./sass/tmpApp.css";
import { motion } from "framer-motion";
//import "./styles.css"

function ShipObj({ time, ...props }) {
  return (
    <>
      {/* <PerspectiveCamera makeDefault fov={50} position={[3, 2, 5]} /> */}
      <Html distanceFactor={10}>
        <motion.div
          distanceFactor={10}
          className="row-col"
          initial={{ opacity: 0, y: -80 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ ease: "easeInOut", duration: 1, delay: 0.4 }}
        >
          <span className="content">ship one</span>
        </motion.div>
      </Html>
      <Ship {...props} />
      {/* <Html distanceFactor={10}>
        <div className="content">ship nomber 1</div>
      </Html> */}

      {/* <mesh {...props}>
        <dodecahedronGeometry />
        <meshStandardMaterial roughness={0.75} emissive="#404057" />
       
      </mesh> */}
      {/* </Ship> */}
    </>
  );
}

function Content() {
  const ref = useRef();
  useFrame(
    () =>
      (ref.current.rotation.x =
        ref.current.rotation.y =
        ref.current.rotation.z +=
          0.01)
  );

  return (
    <group ref={ref}>
      <ShipObj position={[-200, 0, 0]} />
      <ShipObj position={[0, -200, -3]} />
      <ShipObj position={[200, 0, 0]} />
    </group>
  );
}

function App() {
  return (
    <Suspense fallback={null}>
      <Canvas shadows dpr={[1, 2]} camera={{ position: [0, 0, 7.5] }}>
        <OrbitControls target={[0, 0.35, 0]} maxPolarAngel={1.45} />
        {/* <Canvas> */}
        <ShipObj />
      </Canvas>
    </Suspense>
  );
}

export default App;
